















































import { Vue, Component } from 'vue-property-decorator';
import users from '../../store/modules/users';

@Component
export default class AppHeader extends Vue {
  get username() {
    return users.username;
  }
  get userId() {
    return users.userId;
  }
  public logout() {
    users.logOut();
    this.$router.push('/');
  }
}
